___CSS_LOADER_EXPORT___.push([module.id, ".wrapper-page{background-color:#fafafa;height:100%;width:100%}.page-heading{display:flex;justify-content:space-between}.page-details{border-right:1px solid rgba(0,0,0,.1);width:256px}.page-details,.page-search{border-bottom:1px solid rgba(0,0,0,.1)}.page-search{width:calc(100% - 256px)}.content-wrapper{display:flex;height:100%;position:relative;width:100%}.content{margin-left:256px;transition:all .3s ease;width:100%}.content.full{margin-left:0;width:100%}@media (max-width:960px){.content{margin-left:0}.page-details{width:200px}.page-search{width:calc(100% - 200px)}}@media (max-width:740px){.page-details{width:180px}.page-search{width:calc(100% - 180px)}}", "",{"version":3,"sources":["webpack://./resources/js/Pages/Admin/Usuarios/index.vue"],"names":[],"mappings":"AA4OA,cAGA,wBAAA,CADA,WAAA,CADA,UAGA,CAEA,cACA,YAAA,CACA,6BACA,CAEA,cAGA,qCAAA,CAFA,WAGA,CAEA,2BAJA,sCAOA,CAHA,aACA,wBAEA,CAEA,iBAEA,YAAA,CAEA,WAAA,CAHA,iBAAA,CAEA,UAEA,CAEA,SAEA,iBAAA,CACA,uBAAA,CAFA,UAGA,CAEA,cAEA,aAAA,CADA,UAEA,CAEA,yBACA,SACA,aACA,CAEA,cACA,WACA,CAEA,aACA,wBACA,CACA,CAEA,yBACA,cACA,WACA,CAEA,aACA,wBACA,CACA","sourcesContent":["<template>\r\n    <div class=\"wrapper-page\">\r\n        <div class=\"page-heading\">\r\n            <div class=\"page-details\">\r\n                <v-toolbar outlined color=\"grey lighten-3\" elevation=\"0\">\r\n                    <v-toolbar-title>\r\n                        <v-app-bar-nav-icon @click=\"drawer = !drawer\">\r\n                            <v-icon>mdi-filter-menu</v-icon>\r\n                        </v-app-bar-nav-icon>\r\n\r\n                        Usuarios\r\n                    </v-toolbar-title>\r\n                </v-toolbar>\r\n            </div>\r\n            <div class=\"page-search\">\r\n                <v-toolbar outlined color=\"grey lighten-5\" elevation=\"0\">\r\n                    <v-text-field v-model=\"usuario_search\" append-icon=\"mdi-magnify\" label=\"Buscar\" hide-details\r\n                        outlined dense></v-text-field>\r\n                </v-toolbar>\r\n            </div>\r\n        </div>\r\n        <div class=\"content-wrapper\">\r\n            <v-navigation-drawer absolute v-model=\"drawer\" color=\"grey lighten-5\">\r\n                <v-container>\r\n                    <v-btn block color=\"primary\">\r\n                        <v-icon left>mdi-plus</v-icon>\r\n                        Nuevo\r\n                    </v-btn>\r\n\r\n                    <div class=\"mt-5\">\r\n                        <h5 class=\"pb-3 grey--text text--darken-2\">\r\n                            Filtar por Rol\r\n                        </h5>\r\n\r\n                        <v-autocomplete v-model=\"rol\" :items=\"roles\" item-text=\"name\" item-value=\"id\"\r\n                            label=\"Seleccione un Area\" clereable outlined dense clearable></v-autocomplete>\r\n\r\n                    </div>\r\n                </v-container>\r\n            </v-navigation-drawer>\r\n            <div class=\"content\" :class=\"drawer ? '' : 'full'\">\r\n                <v-container>\r\n                    <v-card :loading=\"loading_table\">\r\n                        <v-simple-table>\r\n                            <template v-slot:default>\r\n                                <thead class=\"grey lighten-1\">\r\n                                    <tr>\r\n                                        <th class=\"text-left\">Nombres</th>\r\n                                        <th class=\"text-left\">Apellidos</th>\r\n                                        <th class=\"text-left\">Correo</th>\r\n                                        <th class=\"text-left\">Rol</th>\r\n                                        <th class=\"text-left\"></th>\r\n                                    </tr>\r\n                                </thead>\r\n                                <tbody>\r\n                                    <tr v-for=\"(item, index) in list_usuarios\" :key=\"index\">\r\n                                        <td>{{ item.nombres }}</td>\r\n                                        <td>{{ item.apellidos }}</td>\r\n                                        <td>\r\n                                            {{ item.email }}\r\n                                        </td>\r\n\r\n                                        <td>\r\n                                            {{ item.rol_name }}\r\n                                        </td>\r\n\r\n                                        <td>\r\n                                            <v-menu offset-y>\r\n                                                <template v-slot:activator=\"{\r\n                                                    attrs,\r\n                                                    on,\r\n                                                }\">\r\n                                                    <v-btn icon text color=\"primary\" class=\"\" v-bind=\"attrs\" v-on=\"on\">\r\n                                                        <v-icon>\r\n                                                            mdi-dots-vertical\r\n                                                        </v-icon>\r\n                                                    </v-btn>\r\n                                                </template>\r\n\r\n                                                <v-list dense>\r\n                                                    <v-subheader>Opciones</v-subheader>\r\n                                                    <v-list-item-group color=\"primary\">\r\n                                                        <v-list-item v-for=\"(\r\n                                                                val, i\r\n                                                            ) in itemsOptions\" :key=\"i\" @click=\"\r\n                                                                SelectMenu(\r\n                                                                    val.text,\r\n                                                                    item\r\n                                                                )\r\n                                                            \">\r\n                                                            <v-list-item-icon>\r\n                                                                <v-icon v-text=\"\r\n                                                                    val.icon\r\n                                                                \"></v-icon>\r\n                                                            </v-list-item-icon>\r\n                                                            <v-list-item-content>\r\n                                                                <v-list-item-title v-text=\"\r\n                                                                    val.text\r\n                                                                \"></v-list-item-title>\r\n                                                            </v-list-item-content>\r\n                                                        </v-list-item>\r\n                                                    </v-list-item-group>\r\n                                                </v-list>\r\n                                            </v-menu>\r\n                                        </td>\r\n                                    </tr>\r\n                                </tbody>\r\n                            </template>\r\n                        </v-simple-table>\r\n\r\n                        <v-pagination v-model=\"page\" class=\"\" :length=\"pages\"></v-pagination>\r\n                    </v-card>\r\n                </v-container>\r\n            </div>\r\n        </div>\r\n\r\n    </div>\r\n</template>\r\n<script>\r\nimport Layout from \"@/Layouts/AdminLayout\";\r\nimport axios from \"axios\";\r\n\r\nexport default {\r\n    metaInfo: { title: \"Usuarios\" },\r\n    layout: Layout,\r\n    props: {\r\n        roles: Array,\r\n    },\r\n\r\n    data: () => ({\r\n        dialog_asignar: false,\r\n        user_asignar: {},\r\n        itemsOptions: [\r\n            { text: \"Asignar Area\", icon: \"mdi-map-marker-account-outline\" },\r\n            { text: \"Editar\", icon: \"mdi-pen\" },\r\n            { text: \"Eliminar\", icon: \"mdi-delete\" },\r\n        ],\r\n        loading_table: false,\r\n        rol: null,\r\n\r\n        absolute: false,\r\n        drawer: true,\r\n\r\n        page: 1,\r\n        total_result: 0,\r\n        pages: 1,\r\n        usuario_search: \"\",\r\n        list_usuarios: [],\r\n    }),\r\n    computed: {\r\n        size_display: {\r\n            get: function () {\r\n                if (this.$vuetify.breakpoint.width > 960) {\r\n                    this.absolute = false;\r\n                    return true;\r\n                }\r\n                this.absolute = true;\r\n                return false;\r\n            },\r\n            set: function (newValue) { },\r\n        },\r\n    },\r\n    methods: {\r\n        async getUsuarios(\r\n            term = \"\",\r\n            rol = \"\",\r\n            oficina = \"\",\r\n            area = \"\",\r\n            page = 1\r\n        ) {\r\n\r\n            this.loading_table = true;\r\n            let res = await axios.post(\r\n                \"/admin/usuarios/get-usuarios?page=\" + page,\r\n                {\r\n                    term: term,\r\n                    rol: rol,\r\n                    oficina: oficina,\r\n                    area: area,\r\n                }\r\n            );\r\n            console.log(res.data.datos.data);\r\n            this.page = res.data.datos.current_page;\r\n            this.total_result = res.data.datos.total;\r\n            this.pages = res.data.datos.last_page;\r\n\r\n            this.loading_table = false;\r\n\r\n            return res.data.datos.data;\r\n        },\r\n\r\n\r\n    },\r\n    async created() {\r\n        this.drawer = this.$vuetify.breakpoint.width > 960 ? true : false;\r\n        this.absolute = this.$vuetify.breakpoint.width > 960 ? false : true;\r\n        this.list_usuarios = await this.getUsuarios();\r\n    },\r\n\r\n    watch: {\r\n        async usuario_search(val) {\r\n            if (!val) return;\r\n\r\n            let res = await this.getUsuarios(\r\n                val,\r\n                this.rol,\r\n                this.oficina,\r\n                this.area\r\n            );\r\n            this.list_usuarios = res;\r\n        },\r\n        async page(val, old_val) {\r\n            if (!val) return;\r\n            if (val == old_val) return;\r\n            //this.loading_table = true;\r\n            let res = await this.getUsuarios(\r\n                this.usuario_search,\r\n                this.rol,\r\n                this.oficina,\r\n                this.area,\r\n                val\r\n            );\r\n            this.list_usuarios = res;\r\n            //this.loading_table = false;\r\n        },\r\n\r\n        async rol(val) {\r\n            if (!val) await this.getUsuarios();\r\n            let res = await this.getUsuarios(this.usuario_search, val);\r\n            this.list_usuarios = res;\r\n        },\r\n    },\r\n};\r\n</script>\r\n\r\n<style>\r\n.wrapper-page {\r\n    width: 100%;\r\n    height: 100%;\r\n    background-color: #fafafa;\r\n}\r\n\r\n.page-heading {\r\n    display: flex;\r\n    justify-content: space-between;\r\n}\r\n\r\n.page-details {\r\n    width: 256px;\r\n    border-bottom: 1px solid rgba(0, 0, 0, 0.1);\r\n    border-right: 1px solid rgba(0, 0, 0, 0.1);\r\n}\r\n\r\n.page-search {\r\n    width: calc(100% - 256px);\r\n    border-bottom: 1px solid rgba(0, 0, 0, 0.1);\r\n}\r\n\r\n.content-wrapper {\r\n    position: relative;\r\n    display: flex;\r\n    width: 100%;\r\n    height: 100%;\r\n}\r\n\r\n.content {\r\n    width: 100%;\r\n    margin-left: 256px;\r\n    transition: all 0.3s ease;\r\n}\r\n\r\n.content.full {\r\n    width: 100%;\r\n    margin-left: 0;\r\n}\r\n\r\n@media (max-width: 960px) {\r\n    .content {\r\n        margin-left: 0;\r\n    }\r\n\r\n    .page-details {\r\n        width: 200px;\r\n    }\r\n\r\n    .page-search {\r\n        width: calc(100% - 200px);\r\n    }\r\n}\r\n\r\n@media (max-width: 740px) {\r\n    .page-details {\r\n        width: 180px;\r\n    }\r\n\r\n    .page-search {\r\n        width: calc(100% - 180px);\r\n    }\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
